<template>
	<picker mode="multiSelector" disabled: :value="multiIndex" :range="multiArray" @change="handleValueChange"
		@columnchange="handleColumnChange" @cancel='cancel'>
		<slot></slot>
	</picker>
</template>

<script>
	const CHINA_REGIONS = require('./regions.json')
	export default {
		props: {
			// defaultRegions: {
			// 	type: Array,
			// 	default () {
			// 		return []
			// 	}
			// },
			defaultRegionCode: {
				type: [String, Number]
			},
			type:{
				type: [String, Number],
				default:0
			},
			defaultRegion: [String, Number]
		},
		data() {
			return {
				cityArr: CHINA_REGIONS[0].childs,
				districtArr: CHINA_REGIONS[0].childs[0].childs,
				multiIndex: [0, 0, 0],
				isInitMultiArray: true,
				code:null,
				isFirst:true,
			}
		},
		watch: {
			defaultRegion: {
				handler(region, oldRegion) {
					region = region ? region.toString() : '';
					region = region.split(',')[region.split(',').length-1];
					if (Array.isArray(region)) {
						// 避免传的是字面量的时候重复触发
						oldRegion = oldRegion || []
						if (region.join('') !== oldRegion.join('')) {
							this.handleDefaultRegion(region)
						}
					} else if (region && region.length == 6) {
						this.handleDefaultRegion(region)
					} else {
						console.warn('defaultRegion非有效格式')
					}
				},
				immediate: true,
			}
		},
		computed: {
			multiArray() {
				return this.pickedArr.map(arr => arr.map(item => item.name))
			},
			pickedArr() {
				// 进行初始化
				if (this.isInitMultiArray) {
					return [
						CHINA_REGIONS,
						CHINA_REGIONS[0].childs,
						CHINA_REGIONS[0].childs[0].childs
					]
				}
				return [CHINA_REGIONS, this.cityArr, this.districtArr];
			}
		},
		methods: {
			cancel(){
				if(this.type == 1){
					this.$emit('getRegion', null)
				}
			},
			handleColumnChange(e) {
				this.isInitMultiArray = false;
				const that = this;
				let col = e.detail.column;
				let row = e.detail.value;
				that.multiIndex[col] = row;
				try {
					switch (col) {
						case 0:
							if (CHINA_REGIONS[that.multiIndex[0]].childs.length == 0) {
								that.cityArr = that.districtArr = [CHINA_REGIONS[that.multiIndex[0]]]
								break;
							}
							that.cityArr = CHINA_REGIONS[that.multiIndex[0]].childs
							that.districtArr = CHINA_REGIONS[that.multiIndex[0]].childs[that.multiIndex[1]].childs
							break;
						case 1:
							that.districtArr = CHINA_REGIONS[that.multiIndex[0]].childs[that.multiIndex[1]].childs
							break;
						case 2:
							break;
					}
				} catch (e) {
					that.districtArr = CHINA_REGIONS[that.multiIndex[0]].childs[0].childs
				}
				
			},
			handleValueChange(e) {
				// 结构赋值
				var [index0, index1, index2] = e.detail.value;
				var [arr0, arr1, arr2] = this.pickedArr;
				var  address = {
					code: [arr0[index0].code + '0000', arr1[index1].code + '00', arr2[index2].code],
					name: [arr0[index0].name, arr1[index1].name, arr2[index2].name],
					name_text: arr0[index0].name + '-' + arr1[index1].name + '-' + arr2[index2].name
				};
				this.$emit('getRegion', address)
			},
			handleDefaultRegion(region) {
				const isCode = !Array.isArray(region)
				this.isInitMultiArray = false;
				let children = CHINA_REGIONS
				for (let i = 0; i < 3; i++) {
					for (let j = 0; j < children.length; j++) {
						let condition = isCode ? children[j].code.slice(0, (i + 1) * 2) == region.slice(0, (i + 1) * 2) :
							children[j].name.includes(region[i]);
						if (condition) {
							// 匹配成功进行赋值
							// console.log(i,j,children.length-1);
							children = children[j].childs;
							if (i == 0) {
								this.cityArr = children
							} else if (i == 1) {
								this.districtArr = children
							}
							this.$set(this.multiIndex, i, j)
							break;
						} else {
							// 首次匹配失败就用默认的初始化
							// console.log(i,j,children.length-1);
							if (i == 0 && j == (children.length - 1)) {
								this.isInitMultiArray = true;
							}

						}
					}
					
				}
				if( this.isFirst){
					this.isFirst = false
					let [index0, index1, index2] = this.multiIndex;
					let [arr0, arr1, arr2] = this.pickedArr;
					let address = {
						code: [arr0[index0].code + '0000', arr1[index1].code + '00', arr2[index2].code],
						name: [arr0[index0].name, arr1[index1].name, arr2[index2].name],
						name_text: arr0[index0].name + '-' + arr1[index1].name + '-' + arr2[index2].name
					};
					this.$emit('getRegion', address)
				}
			}
		}
	}
</script>
